	module.exports = {"data":{"wordpressPost":{"title":"See Emma Roberts&#8217; killer new look here","content":"<p><a href=\"http://www.imdb.com/name/nm0731075/\">Actress and model Emma Roberts</a> likes to switch it up and often. She&#8217;s generally brunette, but occasionally blonde and in recent months she&#8217;s been rocking the most beautiful shade of red we&#8217;ve ever seen.</p>\n<p>But, as much as we loved it, and we really, really did, we can&#8217;t get enough of her new rich, chocolate locks that are not only the perfect shade of brunette but an incredibly pretty length.</p>\n<p>You see, Emma, and yes, before you ask yes, <a href=\"http://www.imdb.com/name/nm0000210/\"> Julia Roberts</a> is her aunt, not only underwent a radical colour change she added some gorgeous extensions so her newly-coloured locks are now almost waist-length.</p>\n<p>&nbsp;</p>\n<img class=\"alignnone size-medium wp-image-1564\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14175015/Emma-full-insta-JPG-300x193.jpg\" alt=\"\" width=\"300\" height=\"193\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14175015/Emma-full-insta-JPG-300x193.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14175015/Emma-full-insta-JPG-768x495.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14175015/Emma-full-insta-JPG.jpg 927w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p>&nbsp;</p>\n<p><strong><em>This is what the actress looked like just a month or so ago. That perfect Hollywood glamour red and sitting just at her shoulder. Swoon. Image: Instagram/@emmaroberts</em></strong></p>\n<p>Emma posted a picture of herself on her Instagram account looking pretty damn pleased with her new look and added the caption, &#8216;Been a long time since I had hair this long&#8217;. She also tagged the celebrity salon <a href=\"https://www.instagram.com/ninezeroone/?hl=en\">Nine Zero Salon </a>which is where this hair miracle occurred.</p>\n<p>&nbsp;</p>\n<img class=\"size-medium wp-image-1566\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14180246/Emma-no-words-JPG-250x300.jpg\" alt=\"\" width=\"250\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14180246/Emma-no-words-JPG-250x300.jpg 250w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14180246/Emma-no-words-JPG.jpg 494w\" sizes=\"(max-width: 250px) 100vw, 250px\" />\n<p>&nbsp;</p>\n<p><strong><em>Emma today, rocking the perfect colour and the perfect length. #jealous Image: Instagram/@emmaroberts</em></strong></p>\n<p>According to a report in <a href=\"http://www.allure.com/story/emma-roberts-brunette-hair\">Allure</a>, Emma chose to use <a href=\"http://www.greatlengths.com.au\">Great Lengths hair extensions</a>. This particular style of extension is made from human hair that&#8217;s ethically sourced so we&#8217;re well on board with that. We&#8217;re also totally good with this new look. In fact, like we said &#8211; we&#8217;re kinda jealous.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14181318/Emma-red-JPG1.jpg"},"categories":[{"slug":"hair","name":"Hair"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"6086f3e3-5f56-52cf-bdca-3241bc189e45"}}

